import React, { useMemo, memo } from 'react';
import classNames from 'classnames';
import FocusableComponent from './Focusable';

const Button = props => {
  const { current, focused, text = '' } = props;
  const ButtonClassName = useMemo(() => {
    return classNames({
      'button': true,
      'is-current': current,
      'is-focused': focused
    });
  }, [current, focused]);

  return (
    <div className={ButtonClassName} style={{
      height: '40px',
      width: '100px',
      border: `1px solid ${focused ? 'blue' : '#eee'}`
    }}>
      <span className="button__text">{text}</span>
    </div>
  );
};

export default memo(props => <FocusableComponent Component={Button} {...props} />);
