import React from 'react';
import PropTypes from 'prop-types';
import { withFocusable } from '@noriginmedia/react-spatial-navigation';

const Focusable = ({ Component, ...props }) => {
  return <Component {...props} />;
};

Focusable.propTypes = {
  Component: PropTypes.func
};

const FocusableComponent = withFocusable()(Focusable);

const FocusableWrapper = props => {
  const { handleClick, handleArrowPress, handleBecameFocused, handleBecameBlurred, handleWillUnmount } = props;
  return (
    <FocusableComponent
      {...props}
      onClick={handleClick}
      onEnterPress={handleClick}
      onArrowPress={handleArrowPress}
      onBecameFocused={handleBecameFocused}
      onBecameBlurred={handleBecameBlurred}
      onWillUnmount={handleWillUnmount}
      trackChildren
    />
  );
};

FocusableWrapper.propTypes = {
  disabled: PropTypes.bool,
  setFocus: PropTypes.func,
  handleClick: PropTypes.func,
  handleArrowPress: PropTypes.func,
  handleBecameFocused: PropTypes.func,
  handleBecameBlurred: PropTypes.func,
  handleWillUnmount: PropTypes.func
};

export default FocusableWrapper;
